import wrap from '@/components/wrap';
import Notify from '../../wxcomponents/vant/notify/notify';
export default {
  methods: {
    showNotify() {
      Notify('通知内容');
    },
    showNotify2() {
      Notify({
        duration: 1000,
        text: '通知内容',
        selector: '#custom-selector',
        backgroundColor: '#1989fa',
      });
    },
  },
  components: {
    wrap,
  },
};
